var globalAdultPrice = 22.5;
var globalChildPrice = globalAdultPrice * 0.55;
	var travellers = function() {
		var children = 0;
		var adults = 0;
		var adultCounter = 0;
		var childCounter = 0;
		var adultCharacters = ["img/gfx-passenger-male-2.svg","img/gfx-passenger-female-1.svg","img/gfx-passenger-male-1.svg","img/gfx-passenger-male-3.svg"];
		var childCharacters = ["img/gfx-passenger-child-1.svg"];
		var totalAmount = 1;
		
		function control(e) {
			// $(".stage").css("width","auto");
			var target = e.target;
			var value = -1;
			var adultLabel = "adults";
			var childLabel = "children";

			e.preventDefault();
			category = e.target.parentNode.className;
			if (!$(".travellers").hasClass("activated")) {
				$(".travellers").addClass("activated");
			}
			

			if (e.target.innerHTML=="+") {
				value=1;
				if (category =="adults") {

					
					if (totalAmount % 2) {
						$(".travellers .stage").append('<img src="'+adultCharacters[adultCounter]+'" class="adult">');
						setTimeout(function() {
							$(".travellers .stage .adult:last").addClass("added");
							setTimeout(function(elm) {
								elm.addClass("inplace");
							},900,$(".travellers .stage .adult:last"));
						},200);
					} else {
						$(".travellers .stage").prepend('<img src="'+adultCharacters[adultCounter]+'" class="adult">');
						setTimeout(function() {
							$(".travellers .stage .adult:first").addClass("added");
							setTimeout(function(elm) {
								elm.addClass("inplace");
							},900,$(".travellers .stage .adult:first"));
						},200);
					}
					adultCounter++;	
				} else {
					
					if (totalAmount % 2) {
						$(".travellers .stage").append('<img src="'+childCharacters[childCounter]+'" class="child">');
						setTimeout(function() {
							$(".travellers .stage .child:last").addClass("added");
							setTimeout(function(elm) {
								elm.addClass("inplace");
							},900,$(".travellers .stage .child:last"));
						},200);
					} else {
						$(".travellers .stage").prepend('<img src="'+childCharacters[childCounter]+'" class="child">');
						setTimeout(function() {
							$(".travellers .stage .child:first").addClass("added");
							setTimeout(function(elm) {
								elm.addClass("inplace");
							},900,$(".travellers .stage .child:first"));
						},200);
					}
					childCounter++;
				}

			} else {
				value=-1;			
				if (category =="adults") {
					if (totalAmount % 2) {
						
						setTimeout(function(elm) {
							elm.remove();
						},850,$(".travellers .stage .adult.added").filter(":last"));
						$(".travellers .stage .adult.added").filter(":last").removeClass("added");
					} else {
						
						setTimeout(function(elm) {
							elm.remove();
						},850,$(".travellers .stage .adult.added").filter(":first"))
						$(".travellers .stage .adult.added").filter(":first").removeClass("added");
					}
					adultCounter--;
				} else {
					if (totalAmount % 2) {
						setTimeout(function(elm) {
							elm.remove();
						},850,$(".travellers .stage .child.added").filter(":last"));
						$(".travellers .stage .child.added").filter(":last").removeClass("added");
					} else {
						setTimeout(function(elm) {
							elm.remove();
						},850,$(".travellers .stage .child.added").filter(":first"))
						$(".travellers .stage .child.added").filter(":first").removeClass("added");
					}
					childCounter--;
				}
			}
			if (adultCounter<0) adultCounter=0;
			if (childCounter<0) childCounter=0;
			if (adultCounter>=adultCharacters.length) adultCounter=0;
			if (childCounter>=childCharacters.length) childCounter=0;
			if (category =="adults") {
				adults+=value;
			} else {
				children+=value;
			}
			
			if (adults<0) adults=0;
			if (children<0) children=0;
			totalAmount = adults + children;

			$("input#adults").val(adults);
			$("input#children").val(children);

			if (adults==1) {
				adultLabel= "adult";
			} else {
				adultLabel="adults";
			}
			if (children==1) {
				childrenLabel="child";
			} else {
				childrenLabel="children";
			}
			var priceAdults = adults * globalAdultPrice;
			var priceChildren = children * globalChildPrice;
			var priceTotal = parseFloat(Math.round(10*(priceAdults+priceChildren))/10);
			console.log(adults, priceAdults);
			$("header.controls .price .amount").html("£"+priceTotal.toFixed(2));
			$(".planning button.buy .amount").html("£"+priceTotal.toFixed(2));

			$(".travellers_info").html(adults + " " + adultLabel +", "+ children + " "+ childrenLabel);
			$(".schedule_container .passengers").html(adults + " " + adultLabel +", "+ children + " "+ childrenLabel);
			localStorage.setItem("adults",adults);
			localStorage.setItem("children",children);
			$(".travellers_button button .adults").html(adults);
			$(".travellers_button button .children").html(children);
			
		}
		function scrollHandler(e) {
			var scrollTop = $(window).scrollTop();
			var scrollPoint = 10;
			if (scrollTop>scrollPoint && !$("body").hasClass("scrolled")) {
				$("body").addClass("scrolled");
			} else {
				if (scrollTop<=scrollPoint) {
					$("body").removeClass("scrolled");
				}
			}
		}
		function showRailcards() {
			$(".railcards .choices").css("height",$(".railcards .choices")[0].scrollHeight);
		}
		function travellerToggle(e) {
			$(".row.travellers").toggleClass("visible");
			
			
		}
		function detectFieldsCompleted(e) {
			if ($("#from").val().length>0 && $("#to").val().length>0) {
				$("body.planning").addClass("full");
				setTimeout(function() {
					$("body.planning > h2").html("When do you want to travel?");
				},400);
			} else {
				$("body.planning").removeClass("full");
				$("body.planning > h2").html("Where do you want to go?");
			}
		}
		return {
			Init: function() {
				$(".travellers_button button").bind("click",travellerToggle);
				$(".travellers a").bind("click",control);
				$(".travellers .adults a:last-child").trigger("click");

				$(".travellers .close-modal").bind("click",travellerToggle);
				$("body.planning input").bind("keyup",detectFieldsCompleted);
				$("body.planning input").bind("click",function() {
					setTimeout(detectFieldsCompleted,100);
				});
				$(".swap").bind("click",function(e) {
					var tempTo = $("input#to").val();
					$("input#to").val($("input#from").val());
					$("input#from").val(tempTo);
					e.preventDefault();
					e.stopPropagation();
					return false;
				});
				$(window).bind("scroll",scrollHandler);
				scrollHandler();
				$(".railcards .add").bind("click",showRailcards);
			}
		}
	}();
$(function() {
	travellers.Init();
	$("body.planning").bind("touchstart",eccentricGuy);
	$("body.planning .eccentric_route").bind("touchstart",function(e) {
		e.stopPropagation();
	});
	function eccentricGuy(e) {
		console.log(e.target);
		$("body.planning").unbind("touchstart",eccentricGuy);
		$("img.eccentric_route").addClass("out");
	}
});