var lastActiveScreen = "home";
$(function() {
	
	$(".controls").css("opacity",1);
	stepAnimate("body");
	function enterSplash() {
		$(".screen").removeClass("active");
		$(".splash.screen").css("display","block").css("opacity",1).addClass("active");
		stepAnimate(".splash.screen");
		setTimeout(function() {
			$(".splash").css("opacity",0);
		},4000);
		setTimeout(function() {
			enterLocating();
		},4400);
	}
	
	function loadIntro() {
		$(".screen").removeClass("active");
		$(".station_info").css("display","block").addClass("active");
		$(".locating").css("display","none");
		
		setTimeout(function() {
			$(".intro .gfx").css("transform","translateY(0)");
		},300);
		setTimeout(function() {
			$(".controls").css("opacity",1);
		},500);
		$(".station_info .opacityZero").each(function(i, e) {
			setTimeout(function(obj) {
				$(obj).animate({ opacity: 1},300);
			},(i*270)+500,e);
		});
		$(".intro .gfx").css("display","block");
		setTimeout(function() {
			$(".intro .gfx").css("opacity",1);
		},100);

	
		$(".choose_destination").click(function() {
			$(".screen.search_station input.station").focus();
		
			$(".choose_destination").css("transition","all 0.7s cubic-bezier(.85,.01,.99,.38)");
			$(".choose_destination").addClass("disappear");
			
			hideMainBackground();
			setTimeout(function() {
				enterDestinationScreen();
			},700);
		});
	}
	function hideMainBackground() {
		$(".intro .people").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
		setTimeout(function() {
			$(".intro .background").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(800px)");
		},100);
		setTimeout(function() {
			$(".screen.station_info").animate({ opacity: 0},300).css("pointer-events","none");
		},600);
		$(".station_info .date").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-300px)");
		setTimeout(function() {
			$(".station_info .station").css("transition-duration","0.6s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-300px)");
		},100);
	}
	function enterDestinationScreen() {
		stepAnimate(".search_station");
		$(".search_1 .background").css("transition-duration","0s").css("transition-timing-function","ease").css("transform","translateY(-20px)");
		$(".search_1 .people").css("transition-duration","0s").css("transition-timing-function","ease").css("transform","translateY(5px)");
		setTimeout(function() {
			$(".search_1 .background").css("transition-duration","2s");
			$(".search_1 .people").css("transition-duration","2s");
		},100);
		$(".station_info").css("display","none");
		$(".search_1, .search_1 .gfx").css("display","block");
		setTimeout(function() {
			$(".search_1").css("opacity",1);
		},100);
		setTimeout(function() {
			$(".screen.search_station").css("opacity",0).css("display","block").animate({ opacity: 1},300);
			$(".search_1 .gfx").css("opacity",1);
			$(".search_1").css("transform","translateY(0px)");
		},200);
		setTimeout(function() {
			$(".search_station .results").css("transform","translateY(0px)");
		},350);
	}
	$("body.home div.changes").bind("click",function() {

		setTimeout(function() {
			$(this).css("opacity",0);
		},300);
		closeHomeScreen();
		enterJourneyScreen();
		$(".burger").addClass("close-button");
	});

	$(".home.screen .get_tickets button").bind("click",function() {
		closeHomeScreen();
		setTimeout(function() {
			enterPlanning()
		},1200);
	});
	function rotateInfo() {
		$(".home .info").each(function() {
			var info = $(this);
			if ($("span",info).length>1) {
				$("span:first-child",info).css("transition-duration","0.5s").css("transform","translateY(-42px)");
				$("span:last-child",info).css("transition-duration","0.5s").css("transform","translateY(-42px)");
				setTimeout(function() {
					$("span:last-child",info).prependTo(info);
					$("span:first-child",info).css("transition-duration","0s").css("transform","translateY(0px)");
					$("span:last-child",info).css("transition-duration","0s").css("transform","translateY(0px)");
				},500);
			}
		});
			
		setTimeout(rotateInfo,7000);
	}
	setTimeout(rotateInfo,7000);
});
function enterLocating() {
	document.location.hash="locating";
	console.log("locating");

	$(".route_vertical button").css("opacity",1);
	$(".route_vertical").addClass("active");

	stepAnimate(".locating");
	
	$(".route_vertical button.next").bind("click",function() {
		$(".route_container").removeClass("on");
		setTimeout(function() {
			document.location.href="scanner.php";
		},300);
	});
	$(".route_vertical button.secondary").bind("click",function() {
		$(".route_vertical").removeClass("active");
		$(window).unbind('hashchange', hashChangeHandler);
		openDestinationScreen();
		$(window).bind('hashchange', hashChangeHandler);
	});
	
	
	setTimeout(function() {
		$(".route_container").addClass("on");
	},300);
	setTimeout(function() {
		$(".route_vertical .first-line").addClass("on");
		$(".route_vertical .blue-circle").filter(":first").addClass("on");

		$(".route_vertical .first_station").addClass("on");
	},700);
	setTimeout(function() {
		$(".route_vertical .second-line").addClass("on");
		$(".route_vertical .change-stop").addClass("on");
		$(".route_vertical .change_station").addClass("on");
	},1500);
	setTimeout(function() {
		$(".route_vertical .blue-circle").filter(":last").addClass("on");
		$(".route_vertical .last_station").addClass("on");
		$(".route_vertical .last_station small").addClass("blink");
	},2200);
	setTimeout(function() {
		$(".route_vertical .third-line").addClass("on");
	},2400);
	setTimeout(function() {
		$(".route_vertical .fourth-line").addClass("on");
	},3000);
	setTimeout(function() {
		$("p strong").html("2hr 35min");
	},3500);
	setTimeout(function() {
		$("footer").addClass("active");
	},4500);
}
function locatingToScanner() {
	$(".route_vertical").removeClass("active");
	setTimeout(function() {
		document.location.href="scanner.php";
	},300);
}
function openDestinationScreen() {
	document.location.hash="";
	setTimeout(function() {
		$("section.helper").removeClass("inactive");
	},300);
	setTimeout(function() {
		$("div.search_station").css("display","block");
	},600);
	setTimeout(function() {
		$(".mobile").css("display","block");
		$("div.search_station").css("opacity",1);
	},900);
	$("footer").removeClass("active");
}
function closeDestinationScreen() {
	setTimeout(function() {
		$("section.helper").addClass("inactive");
	},300);
	setTimeout(function() {
		$("div.search_station").css("opacity",0);
	},600);
	setTimeout(function() {
		$("div.search_station").css("display","none");
		$(".mobile").css("display","none");
	},900);
}
function closeHomeScreen() {
	$(".get_tickets, div.changes, .scroll-indicator").css("opacity",0);
	$("body.home .top").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(-500px)").css("opacity",0);
	$("body.home .bottom").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(.73,0,1,.46)").css("transform","translateY(500px)").css("opacity",0);
	
}
first=true;
function enterHomeScreen(delay) {
	console.log("enter home");
	lastActiveScreen="home";
	$("body.home .opacityZero").each(function(i, e) {
		
			$(e).css("opacity", 1);
		
	});
	if (typeof delay == "undefined") delay=1200;
	setTimeout(function() {
		// $("body.home").css("opacity",0).css("display","block").animate({ opacity: 1},300);
		setTimeout(function() {
			$(".home div.changes, .scroll-indicator").css("opacity",1);
		},350);
		if (first) {
			clock_slider.Init($(".clock_slider").eq(0),0);
			first = false;
		}
	},(delay));
}

function enterJourneyScreen() {
	
	$(".home div.changes").css("opacity",0);
	$(".journey .opacityZero").each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).animate({ opacity: 1},300);

		},(i*270)+500,e);
	});
	$(".journey").addClass("active");
	$(".journey").scrollTop(($(".journey")[0].scrollHeight - $(window).height())/2);
	$(".journey ul li:nth-child(4)").addClass("travel");
	$(".journey ul .current_status").addClass("travel");
}
function closeWhatEver() {

	document.location.href='scanner.php';
}
function closeJourneyScreen() {
	$(".travel").removeClass("travel");
	$(".journey").removeClass("active");
}
function stepAnimate(container) {
	$(".opacityZero",$(container)).each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).css("opacity", 1);
		},(i*100),e);
	});
}
function stepAnimateFast(container) {
	$(".opacityZero",$(container)).each(function(i, e) {
		setTimeout(function(obj) {
			$(obj).css("opacity",1);
		},(i*170),e);
	});
}
function enterPlanning() {
	lastActiveScreen="planning";
	$(".planning.screen").css("opacity",0);
	$(".planning.screen").css("display","block");
	$(".screen.planning").animate({ opacity: 1},300);
	stepAnimate(".planning.screen");
}
function enterSchedule() {
	setTimeout(schedule.sizeUp,1000);
	$(".screen.planning").animate({ opacity: 0},300)
	setTimeout(function() {
		$(".screen.schedule .faded").removeClass("faded");
		$(".screen.planning").css("display","none");
		$(".screen.schedule").css("display","block").animate({ opacity: 1},300).addClass("active");
		$(".screen.schedule .schedule_container").removeClass("step1 step2 step3 step4 step5");
		$(".screen.schedule .schedule_container").addClass("step1");
	},300);
}
function closeSchedule() {
	$(".screen.schedule").animate({ opacity: 0},300);
	setTimeout(function() {

		$(".screen.schedule .schedule_container").removeClass("step1 step2 step3 step4");
		$(".screen.schedule").css("display","none");
	},300);
}

function initiateScreen() {
	if (first) {
		clock_slider.Init($(".clock_slider").eq(0),0);
		first = false;
	}
}
$(function() {
	$("body.home").each(initiateScreen);
})
