
$(function() {
	$("a.filters").bind("click",function(e) {
		$(".swt_modal").addClass("active");
		$(".swt_modal > div").removeClass("active");
		$(".swt_modal .departure_controls").addClass("active");
		$(".swt_modal").removeClass("trainmessage");
		$("a.burger").addClass("close-button");
		e.preventDefault();
		return false;
	});
	$("a.burger").bind("click",function(e) {
		$(window).unbind('hashchange', hashChangeHandler);
		$("header.controls").removeClass("toolactive navigateactive");
		if ($(this).hasClass("close-button")) {
			if ($(".swt_modal").hasClass("active")) {
				$(".swt_modal").removeClass("active train_message");
			} else {
				closeJourneyScreen();
				$("body").removeClass("helper");
				setTimeout(enterHomeScreen,550);
					setTimeout(function() {
					setTimeout(function() {
						$(".home .top").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
						$(".home .bottom").css("transition-duration","0.4s").css("transition-timing-function","cubic-bezier(0,.91,.12,.99)");
					},100);
					setTimeout(function() {
						$(".home .top").css("transform","translateY(0)").css("opacity",1);
						$(".home .bottom").css("transform","translateY(0)").css("opacity",1);
					},110);
				},200);
			}
			$(this).removeClass("close-button");
		} else if ($(this).hasClass("signpost")) {
			if ($("section.helper").hasClass("choices") || $("section.helper").hasClass("profile")) {
				// $("section.helper").removeClass("choices profile");

				document.location.hash="";
				setTimeout(function() {
					$("body").removeClass("helper");
					$("header .price").removeClass("inactive");
					$("a.burger").removeClass("signpost");
					$("section.helper").removeClass("active")
					$("div.search_station").removeClass("inactive");
					$("section.helper").bind("click",function() {
						$("section.helper").addClass("active");
						$("div.search_station").addClass("inactive");
						$("header.controls").addClass("toolactive");
						$(".burger").addClass("signpost");
					});
				},450);
			} else {
				document.location.hash="";
				$("body").removeClass("helper");
				$("header .price").removeClass("inactive");
				$(this).removeClass("signpost");
				$("section.helper").removeClass("active")
				$("div.search_station").removeClass("inactive");
				$("section.helper").bind("click",function() {
					$("body").addClass("helper");
					$(this).addClass("active");
					$("div.search_station").addClass("inactive");
					$("header.controls").addClass("toolactive");
					$(".burger").addClass("signpost");
				});
			}
			$(".choice_info").html("");
		} else {
			$("section.helper").unbind();
			$("section.helper").addClass("active")
			$("header .price").addClass("inactive");
			$("body").addClass("helper");
			$(this).addClass("signpost");
			$("div.search_station").addClass("inactive");
			$("header.controls").addClass("toolactive");
			$("section.helper").unbind();
			document.location.hash="helper";
		}
		$(window).bind('hashchange', hashChangeHandler);
		e.preventDefault();
		return false;
	});
	$("section.helper").bind("click",function() {
		$(window).unbind('hashchange', hashChangeHandler);
		document.location.hash="helper";
		$(this).addClass("active");
		$("div.search_station").addClass("inactive");
		$("header.controls").addClass("toolactive");
		$(".burger").addClass("signpost");
		$("section.helper").unbind();
		$(window).bind('hashchange', hashChangeHandler);
	});
	$(".choices-menu button.journey_alerts").bind("click",function() {
		$(window).unbind('hashchange', hashChangeHandler);
		document.location.hash="journey_alerts";
		$("header.controls").removeClass("toolactive").addClass("navigateactive");
		$("section.helper").addClass("choices");
		setTimeout(function() {
			$(".choice_info").html("Journey alerts");
		},500);
		$(window).bind('hashchange', hashChangeHandler);
	});
	
	$(".controls a.profile").bind("click",function(e) {
		if ($(".touch_test").css("display")=="inline") {
			e.preventDefault();
			document.location.href="profile.php";
		} else {
			$(window).unbind('hashchange', hashChangeHandler);
			document.location.hash="profile";
			$("header.controls").addClass("navigateactive").removeClass("toolactive");
			$("section.helper").addClass("profile");
			setTimeout(function() {
				$(".choice_info").html('Good morning<br><span class="name">John</span><br><small>Log out</small>');
			},500);
			$(window).bind('hashchange', hashChangeHandler);
		}
		
	});
});


