var nullObject = new Object();
localStorage.setObject("cheapest_outbound",nullObject);
localStorage.setObject("cheapest_returning",nullObject);
localStorage.setObject("selected_outbound",nullObject);
localStorage.setObject("selected_returning",nullObject);
var currentCheapestPrice = 0;

