function formatDate(date) {
	var date = new Date(date);
	var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
	var days = ["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
	
	var year = date.getYear()+1900;
	var month = months[date.getMonth()];
	var day = days[date.getDay()];
	return day + ", " + month + " " + nth(date.getDay());
}
function offerHandler(e) {
	console.log(e.target);
	e.stopPropagation();
	$(".swt_modal .train_message").html($(".offer_icon",$(e.target)).html()+"<h3>"+$(".header",$(e.target)).html() + "</h3>" + "<p>"+$(".offer_message p",$(e.target)).html()+"</p>");
	$(".swt_modal").addClass("active trainmessage");
	$("a.burger").addClass("close-button");
}
function nth(d) {
  if(d>3 && d<21) return d+'th'; // thanks kennebec
  switch (d % 10) {
        case 1:  return d+"st";
        case 2:  return d+"nd";
        case 3:  return d+"rd";
        default: return d+"th";
    }
} 
	
