var keyboardTimeout;
var searchTimeout;
$(function() {
    var test = document.createElement("div");
    test.id = "fontTest";
    test.style.width = "calc(100% - 60px)";
    test.style.height = "100px";
    test.style.whiteSpace = "nowrap";
    test.innerHTML = "<span></span>";
    test.style.position = "absolute";
    test.style.top = "-100px";
    document.body.appendChild(test);
    // $("input.station").bind("touchstart",function() { $(this).focus(); });
    $(test).css("-webkit-transform", "translateY(-3000px)");
    $(test).textfill({
        maxFontPixels: 80
    });
    $("input.station").bind("keyup", update);

    function update(e) {
        clearTimeout(keyboardTimeout);
        var value = $("input.station").val();
        if (value == "") value = "a";
        // $("#fontTest").html("<span>" + value + "_</span>");
        // $("#fontTest").delay(20).textfill({
        //     maxFontPixels: 80
        // });
        // $("input.station").css("font-size", $("#fontTest span").css("font-size"));
        stationSearch(value);
    }

    function stationSearch(value) {
        $("ul.list").html("<li><span class=\"searching\"></span></li>");
        clearTimeout(searchTimeout);
        searchTimeout = setTimeout(function() {
            $("ul.list").css("opacity", 0).html('<li style="opacity:0" class="clickaction"><a href="javascript:void(0);">Culver Parade</a></li><li style="opacity:0" class="remove" class="clickaction"><a href="javascript:void(0);"">Culver, Road</a></li>');
            $("ul.list").animate({
                opacity: 1
            }, 300);
            $("ul.list li").each(function(i, e) {
                setTimeout(function(obj) {
                    $(obj).animate({
                        opacity: 1
                    }, 300);
                }, i * 200 + 200, e);
            }).bind("click", function() {
                closeDestinationScreen();
                // enterHomeScreen();
                
                setTimeout(function() {
                    enterLocating();

                },1600);
            });
        }, 1000);
        keyboardTimeout = setTimeout(removeKeyboardFocus, 1500);
    }
    

    function removeKeyboardFocus() {
        $("input.station").blur();
    }
    $("#return").bind("focus", function() {
        $(".extra").addClass("open");
        console.log("first " + window.innerHeight);
        setTimeout(function() {
            $(".extra").css("top", $(window).scrollTop());
            console.log("second " + window.outerHeight, $(window).scrollTop());
        }, 300);

    });
    $("#return").bind("blur", function() {
        $(".extra").removeClass("open");
    });
    $("input[type=date]").bind("change",function() {
        setFormattedDate($(this).next().next()[0],this);
    });
    $(".planning radiogroup input[type=radio]").bind("click",function() {
        $(this).prop('checked', true);
        switch ($("input[type=radio]:checked",$(this).parent()).attr("value")) {
            case 'single':
                $(".dates").removeClass("double");
                $(".dates .return input").prop("disabled",true);
                thisAdultPrice =  globalAdultPrice;
                globalChildPrice = globalAdultPrice * childFactor;
                break;
            case 'return':
                $(".dates .return input").prop("disabled",false);
                if ($(".return input[type=text]").val()=="Open") {
                     $(".return input[type=text]").val("");
                }
                thisAdultPrice =globalAdultPrice*2;
                globalChildPrice = globalAdultPrice * childFactor;
                setFormattedDate($(".return input[type=text]"), $(".return input[type=date]"));
               $(".dates").addClass("double");
                break;
            case 'open-return':
                $(".dates").addClass("double");
                $(".return input[type=text]").val("Open");
                break;
        }
        var priceAdults = adults.value * thisAdultPrice;
        var priceChildren = children.value * globalChildPrice;
        var priceTotal = parseFloat(Math.round(10*(priceAdults+priceChildren))/10);
        $("header.controls .price .amount").html("£"+priceTotal.toFixed(2));
        $("button.buy .amount").html("£"+priceTotal.toFixed(2));
        
    });
    function setFormattedDate(target, source) {
        if ($(source).val().length>0) {
            var today = new Date();
            var minDate = new Date(source.min);
            // minDate.setMinutes(minDate.getMinutes()+839);
            var date = new Date($(source).val());
            // date.setMinutes(date.getMinutes()+900);
            if (minDate>date) {
                date=minDate;
                $(source).val(date);
            }
            
            var timezoneOffset = date.getTimezoneOffset();

            var daySelected = "";
            var months = ["Jan","Feb","Mar","Apr","May","Jun","Jul","Aug","Sep","Oct","Nov","Dec"];
            // var months = ["January","February","March","April","May","June","July","August","September","October","November","December"];

            if ($(source).attr("id")=="outbound") {
                localStorage.setItem("outbound", date);
                var compareDate = new Date($("#return").val());
                document.getElementById('return').min=$(source).val();
                if (date>compareDate) {
                    document.getElementById('return').value="";
                    $("#return").next().next().val("");
                } else if ( $("#return").next().val().length==0) {
                    $("#return").attr("value",$(source).val());
                }
            } else {
                localStorage.setItem("returning", date);
            }
           
            date.setMinutes(date.getMinutes()+timezoneOffset);
            if (date.getDate() == today.getDate() && date.getMonth() == today.getMonth() && date.getYear() == today.getYear()) {
                daySelected = "Today";
            } else {
                daySelected = months[date.getMonth()]+" "+ date.getDate();
            }
            $(target).val(daySelected);

        }
    }
    function padDigits(number, digits) {
        return Array(Math.max(digits - String(number).length + 1, 0)).join(0) + number;
    }
    $("input[type=date]").each(function() {
        var today = new Date();
        document.getElementById(this.id).min = (today.getYear()+1900) + "-" + padDigits(today.getMonth()+1,2) +"-" + padDigits(today.getDate(),2);
        if ( $(this).prop('type') != 'date' ) {
            $(this).datepicker();
        }
    });

});